<template>
  <div class="todolist">
    <h1>{{ msg }}</h1>
    <input v-model="newItem" v-on:keyup.enter="insert" class="input">

    <h2>未完成</h2>
    <ul>
      <li v-for="(todoItem, index) in todoItems" v-bind:key="todoItem.id" v-if="!todoItem.isFinished"
        v-on:click="toggleFinish(todoItem)">
        {{todoItem.label}}
        <button v-on:click="remove(index)">
          X
        </button>
      </li>
    </ul>

    <h2>已完成</h2>
    <ul>
      <li v-for="(finishedItem, index) in todoItems" v-bind:key="finishedItem.label" v-if="finishedItem.isFinished" class="finished"
        v-on:click="toggleFinish(finishedItem)">
        {{finishedItem.label}}
        <button v-on:click="remove(index)">
          X
        </button>
      </li>
    </ul>
    <!-- <counter></counter> -->
  </div>
</template>

<script>

import Store from './store'
// import Counter from '@/components/counter'

export default {
  name: 'TodoList',
  // components: {
  //   Counter
  // },
  data () {
    return {
      msg: '待办事项',
      todoItems: Store.fetch(),
      newItem: ''
    }
  },
  watch: {
    todoItems: {
      handler: function (items) {
        Store.save(items);
      },
      deep: true
    }
  },
  created: function () {
    // console.log('todoItems is: ' + this.todoItems)
  },
  methods: {
    toggleFinish: function (item) {
      item.isFinished = !item.isFinished;
    },
    insert: function () {
      if(null == this.todoItems || undefined == this.todoItems) {
        this.todoItems = [];
      }

      const trimmedText = this.newItem.trim()
      this.todoItems.push({
        id: this.todoItems.length,
        label: trimmedText,
        isFinished: false
      });
      this.newItem = '';
    },
    remove: function (index) {
      this.todoItems.splice(index, 1);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
  text-align: center;
}
.input {
  width: 100%;
  padding: 8px 10px;
  border: 1px solid #32485F;
}
ul {
  list-style: none;
  padding: 0;
}
li {
  height: 30px;
}
a {
  color: #42b983;
}
.finished {
  text-decoration: underline;
}
</style>



